#home {
	min-height: 100%;
	min-width: 100%;
}

#banner {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	min-height: 692px;
	background-image: url("https://kakaostyle.com/_next/static/images/hero-e170d6e060a6a0cb5a04bc9000292191.jpg");
	background-size: cover;

	color: white;

	& > div {
		width: 1200px;

		h1 {
			font-size: 58px;
		}

		h3 {
			font-size: 26px;
		}

		&:first-child {
			margin-bottom: 25px;
		}
	}
}