#login {
	display: flex;

	gap: 50px;
}

#info {
	display: flex;
	flex-direction: column;
	justify-content: center;

	h3 {
		font-size: 25px;

		margin-bottom: 5px;
	}

	p {
		font-size: 13px;
		color: 7c7c7c;
	}
}

#form {
	border: 1px solid #e5e5e5;
	box-sizing: border-box;
	padding: 60px 70px;

	width: 480px;

	form {
		display: flex;
		flex-direction: column;
		gap: 10px;

		.row {
			input {
				box-sizing: border-box;

				width: 100%;
				padding: 11px 0px 8px 0px;

				border-width: 0px 0px 2px 0px;
				border-color: #ebebeb;

				font-size: 16px;

				&:focus {
					outline: none;

					border-color: black;
				}
			}

			button {
				width: 100%;
				height: 50px;

				border: 0px;
				border-radius: 4px;
				cursor: pointer;

				font-size: 15px;

				background-color: #fee500;
			}

			&.message {
				margin-top: 30px;

				div {
					padding: 20px;

					background-color: #FAFAFA;
					color: #e65f32;

					font-size: 13px;
				}
			}

			&.tip {
				margin-top: 50px;

				text-align: center;

				color: grey;
			}

			&:last-child {
				margin-top: 20px;
			}
		}
	}
}