header {
	width: 100%;
	align-items: center;

	height: 89px;
	background-color: #181818;

	& > div {
		display: flex;
		justify-content: space-between;
		align-items: center;

		color: white;

		padding: 0px 20px;
		margin: auto;
		height: 100%;
		max-width: 1200px;

		box-sizing: border-box;

		& > div {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 20px;

			height: 100%;

			img {
				height: 39px;
			}

			button {
				border: transparent;
				background-color: transparent;
				color: white;

				cursor: pointer;

				transition: 0.3s;

				&:hover {
					color: #fee500;

					transition: 0.3s;
				}
			}
		}
	}
}