footer {
	width: 100%;

	background-color: #181818;
	color: white;

	margin-top: 100px;

	& > div {
		max-width: 1200px;

		padding: 70px 0px;
		margin: auto;

		font-weight: bold;

		h3 {
			font-size: 22px;

			padding: 10px 0px;
		}

		h4 {
			font-size: 14px;

			color: #676767;

			padding: 20px 0px;
		}

		p {
			display: inline-block;

			font-size: 16px;
			margin-right: 50px;

			padding: 5px 0px;

			color: #a2a2a2;

			a {
				color: #a2a2a2;
				text-decoration: none;
			}
		}

		img {
			display: none;
		}
	}
}