#account-card {
	max-width: 1200px;

	margin: auto;
	margin-top: 50px;

	& > div {
		display: flex;
		gap: 30px;
		align-items: center;

		max-width: 320px;

		padding: 30px;
		box-sizing: border-box;

		border: 1px solid #D7E2EB;
		border-radius: 20px;

		img {
			border-radius: 50%;
		}

		p {
			font-size: 18px;

			span {
				font-weight: bold;
				font-size: 26px;
			}
		}
	}
}