#vacation-list {
	display: flex;
	flex-direction: column;
	gap: 40px;

	max-width: 1200px;

	margin: auto;
	margin-top: 50px;
	padding: 30px;
	box-sizing: border-box;

	border: 1px solid #d7e2eb;
	border-radius: 20px;

	.sub {
		color: grey;
	}

	[data-tab="meta"] {
		display: flex;
		justify-content: space-between;

		h3 {
			font-size: 26px;
		}

		.button-group {
			display: flex;
			gap: 8px;
		}

		button {
			padding: 5px 15px;

			border: transparent;
			border-radius: 5px;
			background-color: #fee500;

			font-size: 14px;

			cursor: pointer;

			&.reset {
				background-color: crimson;
				color: white;
			}
		}
	}

	[data-tab="form"] {
		display: flex;
		justify-content: center;
		align-items: center;

		position: fixed;
		top: 0px;
		left: 0px;

		width: 100%;
		height: 100%;

		background-color: rgba($color: #000000, $alpha: 0.7);

		form {
			display: flex;
			flex-direction: column;
			gap: 30px;

			width: 300px;

			margin: auto;
			padding: 40px;

			border-radius: 10px;

			background-color: white;

			input[name="date"] {
				border: transparent;
				border-radius: 50px;
				box-sizing: border-box;

				background-color: #fee500;

				font-size: 16px;
				font-weight: bold;

				padding: 10px 30px;

				width: 100%;

				text-align: center;

				&:focus {
					outline-color: cornflowerblue;
				}
			}

			.group {
				display: flex;
				justify-content: space-around;

				padding: 10px 10px;

				border: 1px solid #bbbbbb;
				border-radius: 5px;
				box-sizing: border-box;

				div {
					display: flex;
					align-items: center;
					gap: 5px;

					input,
					label {
						cursor: pointer;
					}

					input:checked:after {
						background-color: #fee500;
					}

					input[disabled] ~ label {
						color: gainsboro;
					}
				}
			}

			input[name="comment"] {
				border: 1px solid #bbbbbb;
				border-radius: 5px;
				box-sizing: border-box;

				font-size: 16px;

				padding: 5px 10px;

				width: 100%;

				&:focus {
					outline-color: cornflowerblue;
				}
			}

			p {
				text-align: center;
				color: crimson;
				font-size: 14px;
			}

			.buttons {
				display: flex;
				gap: 20px;

				input {
					width: 100%;

					border: transparent;
					border-radius: 5px;
					box-sizing: border-box;

					padding: 5px 10px;

					font-size: 16px;

					cursor: pointer;

					color: white;

					&[type="submit"] {
						background-color: limegreen;
					}

					&[type="reset"] {
						background-color: crimson;
					}
				}
			}
		}

		&[data-status="false"] {
			display: none;
		}
	}

	[data-tab="items"] {
		display: flex;
		flex-direction: column;
		gap: 20px;

		.vacation {
			width: 100%;
			display: flex;

			border-radius: 20px;

			box-shadow: 3px 3px 10px gainsboro;

			.info {
				display: flex;
				justify-content: space-between;

				width: 100%;

				border-right: 1px dashed whitesmoke;
				box-sizing: border-box;

				padding: 30px;

				.meta {
					h3 {
						color: grey;
						font-size: 26px;
					}

					h4 {
						color: grey;
						font-size: 20px;
					}

					div {
						margin-top: 60px;

						p {
							font-size: 14px;
							color: #aaaaaa;
						}
					}
				}

				.detail {
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					width: 500px;

					.days {
						display: flex;
						justify-content: space-between;
						align-items: center;

						.date {
							color: dodgerblue;

							font-weight: bold;
							font-size: 24px;
						}

						.day-text {
							color: dodgerblue;

							text-align: center;
							font-size: 14px;
						}

						.place {
							font-weight: bold;
							font-size: 14px;
						}
					}

					.option {
						font-size: 14px;

						color: #aaaaaa;
					}
				}
			}

			.action {
				display: flex;
				justify-content: center;
				align-items: center;

				width: 100px;

				border-top-right-radius: 20px;
				border-bottom-right-radius: 20px;
				background-color: #fee500;

				button {
					border: transparent;
					background: transparent;

					color: #d2bd00;

					cursor: pointer;

					transition: 0.5s;

					&:hover {
						color: black;
						transition: 0.5s;
					}
				}
			}
		}
	}
}
